import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import ImportExcel from '@/components/ImportExcel'

Vue.use(Router)


export default new Router({
  routes: [
    {
      path: '/',
      name: 'Excel导入',
      component: ImportExcel
    }
  ]
})



