<template>
  <div>
    <input type="file" id="excel-file" value="file"/>
    <el-button @click="exportToExcel">导出EXCEL</el-button>
    <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
    <div style="margin: 15px 0;"></div>
    <el-checkbox-group v-model="checkedTableNames" @change="handleCheckedCitiesChange">
      <el-checkbox v-for="item in tableNames" :label="item" :key="item" @change="handleCheckedCitiesChange">{{item}}</el-checkbox>
    </el-checkbox-group>
    <el-table
      :data="excelMsg"
      style="width: 100%"
      id="table-content"
      height="400"
    >
      <template v-for="item in checkedTableNames">
        <el-table-column
          :prop="item"
          :label="item"
        >
        </el-table-column>
      </template>
      <el-table-column
        prop="msg"
        label="提取区(把成绩全部复制到记事本再发)"
        width="2000"
      >
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  import $ from "jquery"
  import XLSX from "xlsx"
  import FileSaver from 'file-saver'

  var tableNames = [null];
  var fgf = "\xa0\xa0";
  export default {
    name: "ImportExcel",
    data() {
      return {
        checkedTableNames:tableNames,
        checkAll: false,
        excelMsg: [null],
        tableNames: tableNames,
        isIndeterminate: true
      }
    },
    mounted() {
      var that = this;
      $('#excel-file').change(function (e) {
        console.log("ss", that)
        var files = e.target.files;
        var fileReader = new FileReader();
        fileReader.onload = function (ev) {
          console.log(ev, 'ev')
          try {
            var data = ev.target.result,
              workbook = XLSX.read(data, {
                type: 'binary'
              }), // 以二进制流方式读取得到整份excel表格对象
              persons = []; // 存储获取到的数据
          } catch (e) {
            console.log('文件类型不正确');
            return;
          }

          // 表格的表格范围，可用于判断表头是否数量是否正确
          var fromTo = '';
          // 遍历每张表读取
          for (var sheet in workbook.Sheets) {
            if (workbook.Sheets.hasOwnProperty(sheet)) {
              fromTo = workbook.Sheets[sheet]['!ref'];
              console.log(fromTo);
              persons = persons.concat(XLSX.utils.sheet_to_json(workbook.Sheets[sheet]));
              break; // 如果只取第一张表，就取消注释这行
            }
          }

          console.log(persons);
          tableNames =[null];
          for (let key in persons[0]) {
            tableNames.push(key)
          }
          console.log("tt", tableNames)
          that.tableNames = tableNames;

          for (let index in persons){
            let xh = parseInt(index)+1;
            persons[index].msg = xh+"尊敬的"+persons[index].姓名+"家长，以下是您孩子本次月考成绩："
            for (let key in persons[index]) {
              if(key.indexOf("班座")<0 && key.indexOf("姓名")<0 ) {
                persons[index].msg = persons[index].msg + key + " " + persons[index][key] + fgf
              }
            }
            // persons[index].msg=xh+"尊敬的"+persons[index].姓名+"家长，以下是您孩子本次月考成绩："
            //   +"语文 "+persons[index].语文+fgf
            //   +"数学 "+persons[index].数学+fgf
            //   +"英语 "+persons[index].英语+fgf
            //   +"三科总分 "+persons[index].三科分数+fgf
            //   +"三科段名 "+persons[index].三科段名+fgf
            //   +"三科班名 "+persons[index].三科班名+fgf
            //   +"三科进退步 "+persons[index].进退_1+fgf
            //   +"物理 "+persons[index].物理+fgf
            //   +"政治 "+persons[index].政治+fgf
            //   +"历史 "+persons[index].历史+fgf
            //   +"地理 "+persons[index].地理+fgf
            //   +"生物 "+persons[index].生物+fgf
            //   +"八科总分 "+persons[index].八科总分+fgf
            //   +"八科段名 "+persons[index].八科段名+fgf
            //   +"八科班名 "+persons[index].八科班名+fgf
            //   +"八科进退步 "+persons[index].进退
          }

          that.excelMsg = persons;
        };

        // 以二进制方式打开文件
        fileReader.readAsBinaryString(files[0]);
      });
    },
    methods: {
      handleCheckAllChange(val) {
        this.checkedTableNames = val ? tableNames : [];
        this.isIndeterminate = false;
      },
      handleCheckedCitiesChange(value) {
        let checkedCount = value.length;
        this.checkAll = checkedCount === this.tableNames.length;
        this.isIndeterminate = checkedCount > 0 && checkedCount < this.tableNames.length;
      },
      //导出Excel
      exportToExcel () {
        let et = XLSX.utils.table_to_book(document.getElementById('table-content')); //此处传入table的DOM节点
        let etout = XLSX.write(et, {
          bookType: 'xlsx',
          bookSST: true,
          type: 'array'
        });
        try {
          FileSaver.saveAs(new Blob([etout], {
            type: 'application/octet-stream'
          }), 'trade-publish.xlsx');   //trade-publish.xlsx 为导出的文件名
        } catch (e) {
          console.log(e, etout) ;
        }
        return etout;
      }


    }

  }
</script>

<style scoped>

</style>



